import vue from 'vue'
import router from 'vue-router'
import home from '@/views/home.vue'
import articles from '@/views/articles.vue'
import contact from '@/views/contact.vue'

vue.use(router)

const route = new router({
	//mode:'history',   //没有服务器支持就不使用history模式
	routes:[{
		path:"/",
		name:"index - blog",
		component: home,
		meta:{
			//title:'blog'
		}
	},{
		path:"/articles",
		name:"articles - blog",
		component: articles,
		meta:{
			title:'articles - blog'
		}
	},{
		path:"/contact",
		name:"contact - blog",
		component: contact,
		meta:{
			title:'contact - blog'
		}
	}]
	
})

route.beforeEach((to,from,next)=>{
	if(to.meta.title){
		document.title = to.meta.title;
	}
	
	next()
});

export default route; 

